.fuelux .wizard {
  position: relative;
}
.fuelux .wizard .actions {
  background: #f4f5f6;
  background-image: -webkit-linear-gradient(top, #fafafa, #eeeff1);
  background-image: linear-gradient(to bottom, #fafafa, #eeeff1);
  float: none;
  top: 0;
}
.fuelux .wizard .actions .btn-next .glyphicon,
.fuelux .wizard .actions .btn-prev .glyphicon {
  display: none;
}
.fuelux .wizard .actions .btn-prev {
  background: none;
  border: none;
  box-shadow: none;
  color: #2168a7;
}
.fuelux .wizard .actions .btn-prev:hover,
.fuelux .wizard .actions .btn-prev:focus {
  color: #33638c;
}
.fuelux .wizard .steps {
  background: #f4f5f6;
  background-image: -webkit-linear-gradient(top, #fafafa, #eeeff1);
  background-image: linear-gradient(to bottom, #fafafa, #eeeff1);
  *zoom: 1;
}
.fuelux .wizard .steps:before,
.fuelux .wizard .steps:after {
  display: table;
  content: "";
  line-height: 0;
}
.fuelux .wizard .steps:after {
  clear: both;
}
.fuelux .wizard .steps li {
  background: #f3f4f5;
  box-shadow: 0px 0px 0px 1px #fff inset;
}
.fuelux .wizard .steps li:before {
  background: #d4d4d4;
  bottom: -1px;
  content: " ";
  display: block;
  height: 1px;
  position: absolute;
  right: -1px;
  width: 5px;
  z-index: 5;
}
.fuelux .wizard .steps li.active {
  color: #4e8abe;
}
.fuelux .wizard .steps li.active .badge {
  background: #4e8abe;
}
.fuelux .wizard .steps li.active .chevron:before {
  border-color: transparent transparent transparent #fff;
}
.fuelux .wizard .steps li.active .chevron:after {
  border-color: transparent transparent transparent #f1f6fc;
}
.fuelux .wizard .steps li.complete {
  color: #5fb21c;
}
.fuelux .wizard .steps li.complete .badge {
  background: #5fb21c;
  color: #5fb21c;
  position: relative;
}
.fuelux .wizard .steps li.complete .badge:before {
  color: #fff;
  content: "\e013";
  display: block;
  font-family: "Glyphicons Halflings";
  font-size: 10px;
  left: 4px;
  position: absolute;
  top: 4px;
}
.fuelux .wizard .steps li.complete .chevron:before {
  border-color: transparent transparent transparent #fff;
}
.fuelux .wizard .steps li.complete .chevron:after {
  border-color: transparent transparent transparent #f3f4f5;
}
.fuelux .wizard .steps li.complete:hover .chevron:after {
  border-color: transparent transparent transparent #e7eff8;
}
.fuelux .wizard .steps li .badge {
  background: #999;
  padding: 3px 6px;
}
.fuelux .wizard .steps li .chevron:before {
  border-color: transparent transparent transparent #fff;
}
.fuelux .wizard .steps li .chevron:after {
  border: 24px solid transparent;
  border-left: 14px solid #f3f4f5;
  border-right: 0;
  content: "";
  display: block;
  position: absolute;
  right: 2px;
  top: -24px;
}
.fuelux .wizard .steps li .has-secondary-text {
  display: inline-block;
  float: right;
}
.fuelux .wizard .steps li .has-secondary-text h4 {
  font-size: 14px;
  margin-bottom: -1.25em;
}
.fuelux .wizard .steps li .has-secondary-text small {
  color: #ababab;
  font-size: 11px;
  line-height: 1em;
}
